<script setup lang="ts">
// This starter template is using Vue 3 <script setup> SFCs
// Check out https://v3.vuejs.org/api/sfc-script-setup.html#sfc-script-setup
// import { onMounted } from "@vue/runtime-core";
import { onBeforeMount, onMounted, ref, getCurrentInstance } from "vue";
import socket from "./utils/socket";
import { getRandomSixNum } from "./utils";
import { ipcRenderer } from "electron";
import { handleStream } from "./samples/util";
// import useStream from "./utils/stream";
const form: any = ref({
  id: "",
});
const userId: any = ref(null);
const STUNconfig: any = ref({
  iceServers: [
    // { urls: "stun:stun.l.google.com:19302" },
    // Information about ICE servers - Use your own!
    { url: "stun:stun.l.google.com:19302" },
    // {
    //   urls: "stun:stun.stunprotocol.org",
    // },
  ],
});
const myStream: any = getCurrentInstance()?.appContext.config.globalProperties;
const pc: any = ref(null);
const offerOption: any = ref({
  offerToReceiveAudio: false,
  offerToReceiveVideo: true,
});
const localId: any = ref(null);
onBeforeMount(() => {
  console.warn(myStream, "myStream");
  initLocalId();
});
onMounted(() => {
  initVideo();
  initWebSocket();
});

//Id
const initLocalId = () => {
  localId.value = getRandomSixNum();
  // localId.value = localStorage.localId ?? getRandomSixNum();
  localStorage.setItem("localId", localId.value);
};
const initVideo = () => {
  initRtcStream();
};
const check = ref(false);
//
const submit = async () => {
  userId.value = form.value.id;
  // pc.value.onaddstream({ stream: myStream.$myStream });
  pc.value.addStream(myStream.$myStream);
  // pc.value.ontrack({ streams: [myStream.$myStream] });
  // for (const track of myStream.$myStream.getTracks()) {
  //   await pc.value.addTrack(track, myStream.$myStream);
  // }
  //  pc.value.addTrack()
  const offer = await pc.value.createOffer();
  await pc.value.setLocalDescription(offer);
  console.warn("offer", offer);
  socket.emit("receiveOffer", {
    id: form.value.id,
    offer,
    m_id: localId.value,
  });
};

//websocket
const initWebSocket = () => {
  socket.emit("send_my_content", { id: localId.value });
  socket.on("receiveAnswer", (answer: any) => {
    let { type, sdp } = answer;
    let myOffer = new RTCSessionDescription({ type, sdp });
    console.warn("answer", myOffer);
    console.warn("setRemoteDescription");
    pc.value.setRemoteDescription(myOffer);
  });
  socket.on("receiveOffer", async ({ id, offer }: any) => {
    userId.value = id;
    // console.warn(myStream, "myTrack");
    let { type, sdp } = offer;
    let myOffer = new RTCSessionDescription({ type, sdp });
    console.warn("offer", myOffer);
    // pc.onaddstream({ stream: myStream.$myStream });
    // pc.value.ontrack({ streams: [myStream.$myStream] });
    pc.value.addStream(myStream.$myStream);

    //Track
    // for (const track of myStream.$myStream.getTracks()) {
    //   await pc.value.addTrack(track, myStream.$myStream);
    // }
    console.warn("setRemoteDescription");
    await pc.value.setRemoteDescription(myOffer);
    console.warn("answer");
    const answer = await pc.value.createAnswer();
    socket.emit("receiveAnswer", { answer, id });
    console.warn("setLocalDescription");
    await pc.value.setLocalDescription(answer);
  });

  socket.on("icecandidate", async ({ iceCandidate }: any) => {
    if (iceCandidate) {
      console.warn("icecandidate", iceCandidate);
      await pc.value.addIceCandidate(iceCandidate);
    }
  });
};
const initRtcStream = () => {
  // console.warn("StreamTrack");
  window.onbeforeunload = () => {
    socket.emit("userLeave", {
      id: form.value.id,
    });
  };
  console.warn("RTCPeerConnection");
  pc.value = new RTCPeerConnection();
  // pc.value = new RTCPeerConnection(STUNconfig.value);
  pc.value.onaddstream = (event: any) => {
    console.warn("onStream", event);
    // if (e && e.streams) {
    console.warn("/...");
    handleStream(event.stream);
    // }
  };
  // document.querySelector("#video").srcObject = event.stream;
  // };
  // pc.value.ontrack = (e) => {
  //   console.warn("ontrack", e);
  //   if (e && e.streams) {
  //     console.warn("/...");
  //     handleStream(e.streams[0]);
  //     // check.value = true;
  //   }
  // };

  pc.value.onicecandidate = (e: any) => {
    console.warn("onicecandidate", e);
    if (e.candidate) {
      // if (check) return;
      console.warn("candidate", e);
      socket.emit("icecandidate", {
        iceCandidate: e.candidate,
        id: userId.value,
      });
    }
  };
  pc.value.onclose = () => {};
};
const open = () => {
  ipcRenderer.send("open_new_win");
};
const testLocal = () => {
  console.warn(myStream.$myStream, "steams");
  handleStream(myStream.$myStream);
};
</script>

<template>
  <div @click="open"></div>
  <div @click="testLocal"></div>
  <div>
    <span>ID</span>
    <span>{{ localId }}</span>
  </div>
  <div>
    <span>ID</span>
    <input v-model="form.id" />
  </div>
  <div>
    <button @click="submit"></button>
  </div>
  <video id="video"></video>
  <video id="other"></video>
  <!-- <video id="video-other" ></video> -->
</template>

<style>
#video {
  display: block;
}
* {
  margin: 0;
  padding: 0;
}
</style>
